// Gradient

@keyframes gradient {
  0% {
    background-position: 0 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0 50%;
  }
}

@keyframes pulse-gradient {
  0% {
    background-size: 100%;
  }

  100% {
    background-size: 125%;
  }
}

@mixin color-pulse($color-start, $color-end) {
  @keyframes color-pulse {
    0% {
      background-color: $color-start;
    }

    100% {
      background-color: $color-end;
    }
  }
}

@keyframes pulse {
  0% {
    transform: scaleX(1) translateY(-50%);
  }

  100% {
    transform: scaleX(1.05) translateY(-50%);
  }
}

@mixin pulse-icon($color, $app) {
  @keyframes pulse-icon-#{$app} {
    0% {
      box-shadow: 0 0 0 0 $color;
    }
    70% {
      box-shadow: 0 0 0 20px rgba(204, 169, 44, 0);
    }
    100% {
      box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
    }
  }
}

@keyframes flash {
  from,
  50%,
  to {
    opacity: 1;
  }

  25%,
  75% {
    opacity: 0;
  }
}

@keyframes shakeX {
  from,
  to {
    transform: translate3d(0, 0, 0);
  }

  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translate3d(-10px, 0, 0);
  }

  20%,
  40%,
  60%,
  80% {
    transform: translate3d(10px, 0, 0);
  }
}

@keyframes headShake {
  0% {
    transform: translateX(0);
  }

  3.5% {
    transform: translateX(-2px) rotateY(-9deg);
  }

  9.5% {
    transform: translateX(1px) rotateY(7deg);
  }

  15.5% {
    transform: translateX(-2px) rotateY(-5deg);
  }

  22.5% {
    transform: translateX(1px) rotateY(3deg);
  }

  25% {
    transform: translateX(0);
  }
}

//! Bounce

@keyframes bounce {
  0%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  70% {
    transform: translateY(-6px);
  }
  90% {
    transform: translateY(-3px);
  }
}

//! Button
@keyframes button {
  0% {
    transform: skew(-35deg) translateX(0);
  }

  40% {
    transform: skew(-35deg) translateX(850px);
  }

  100% {
    transform: skew(-35deg) translateX(850px);
  }
}

@keyframes fadeEffect {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes rollDown {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes rollUp {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-100%);
  }
}

